html {
  height: 0;
}

nav button {
  text-transform: uppercase;
  background: none;
  border: none;
  height: 60px;
  font-family: Montserrat, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: bold;
  text-decoration: none;
  display: block;
  padding: 6px 12px !important;
}

nav button:hover {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: 1px 2px 5px #000;
}

nav button:active {
  background: #203833;
  border: 1px solid #4c746c;
  border-radius: 10% / 50%;
  box-shadow: inset 1px 2px 5px #000;
}

nav {
  flex-direction: row;
  justify-content: stretch;
  align-items: stretch;
  width: 100%;
  height: 60px;
  display: flex;
  position: static;
  background: #40685f !important;
}

#icoli {
  width: 50px;
  height: 50px;
}

body {
  background-color: #203833;
  background-image: url("../bosque.5663e821.png");
  background-position: top;
  background-size: 100%;
  background-attachment: fixed;
  margin: 0;
}

#content1 {
  color: #fff;
  background-color: #203833;
  padding-left: 50px;
}

#FSPC {
  height: 116px;
}

footer {
  grid-template-columns: 1, 1, 1;
  width: 100%;
  height: 100px;
  display: grid;
  position: fixed;
  bottom: 0;
  background-color: #40685f !important;
}

.foot {
  height: inherit;
  width: 100%;
}

#GF1 {
  float: left;
  height: inherit;
  background-color: #40685f;
  grid-area: 1 / 1;
  width: 100%;
  display: block;
}

#FT {
  color: #a5adab;
  background: none;
  border-color: #162724;
  border-radius: 10% / 50%;
  grid-area: 1 / 2;
  width: 33%;
  padding: 10px 50px;
  font-weight: bold;
  translate: 90% 50%;
}

#FT:hover {
  color: #ecf1f0;
  background-color: #4c746c;
  animation: 1s linear luz;
  box-shadow: 1px 2px 5px #000;
}

#FT:active {
  background: #203833;
  box-shadow: inset 1px 2px 5px #000;
}

#GF2 {
  float: right;
  height: inherit;
  grid-area: 1 / 3;
  width: 33%;
  display: block;
}

